import React from 'react';
import { Link } from 'gatsby';
import Cta from './cta';
import Logo01 from '../images/logo01.png';
import Logo01Sp from '../images/logo01_sp.png';

export default function Header({ page }) {
  return (
    <header className='l-header'>
      <div className='p-header'>
        <h1 className='p-header__logo'>
          <Link
            to='/'
            onClick={() => window.$('body').removeClass('is-gnav-open')}
          >
            <picture>
              <source media='(max-width: 768px)' srcSet={Logo01Sp} />
              <img
                src={Logo01}
                alt='OXWM－オキシウム－'
                width={174}
                height={77}
                loading='eager'
              />
            </picture>
          </Link>
        </h1>
        <div className='p-header__switch'>
          <a href='#' className='js-gnav-switch'>
            <span></span>
            <span></span>
            <span></span>
          </a>
        </div>
      </div>
      <nav className='p-gnav'>
        <div className='p-gnav__menu js-location-check'>
          <ul id='menu-gnav' className='c-menu'>
            {[
              {
                pageType: 'first',
                link: '/first/',
                label: '初めての方へ'
              },
              {
                pageType: 'about',
                link: '/about/',
                label: 'ジム情報'
              },
              {
                pageType: 'course',
                link: '/course/personal/',
                label: 'プラン・料金'
              },
              {
                pageType: 'faq',
                link: '/faq/',
                label: 'よくあるご質問'
              },
              {
                pageType: 'beforeafter',
                link: '/beforeafter/',
                label: 'ビフォーアフター'
              },
              {
                pageType: 'news',
                link: '/news/',
                label: '新着情報'
              },
              {
                pageType: 'recruit',
                link: '/recruit/',
                label: '採用情報'
              }
            ].map(({ pageType, link, label }) => {
              const current = pageType === page;

              return (
                <li
                  key={link}
                  className={`menu-item${current ? ' current_page_item' : ''}`}
                >
                  <Link
                    to={link}
                    onClick={() => window.$('body').removeClass('is-gnav-open')}
                    aria-current={current ? 'page' : false}
                  >
                    <span>{label}</span>
                  </Link>
                </li>
              );
            })}
            <li className='menu-item'>
              <a
                href='https://note.com/oxwm'
                target='_blank'
                rel='noopener noreferrer'
              >
                <span>コラム</span>
              </a>
            </li>
          </ul>
        </div>
        <Cta type={1} />
      </nav>
    </header>
  );
}
