import React, { useEffect } from 'react';
import { Helmet } from 'react-helmet';
import { Link, graphql, useStaticQuery, withPrefix } from 'gatsby';
import loadScript from '../utils/loadScript';
import Header from '../components/header';
import Footer from '../components/footer';
import '../style.css';
import '../custom.css';

export default function Layout(props) {
  const {
    site: { siteMetadata }
  } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
          siteUrl
          description
        }
      }
    }
  `);

  const {
    children,
    page,
    subtitle,
    subtitleEn,
    breadcrumbs,
    title,
    description
  } = props;

  const isTop = page === 'top';
  const pageTitle = title
    ? `${title} | ${siteMetadata.title}`
    : subtitle
    ? `${subtitle} | ${siteMetadata.title}`
    : siteMetadata.title;
  const pageDescription = description ? description : siteMetadata.description;
  const ogpImage = `${siteMetadata.siteUrl}ogp.jpg`;

  useEffect(() => {
    const f = async () => {
      await loadScript(
        'https://code.jquery.com/jquery-3.6.0.min.js',
        'sha256-/xUj+3OJU5yExlq6GSYGSHk7tPXikynS7ogEvDej/m4=',
        'anonymous'
      );
      await loadScript(withPrefix('/plugin.js'));
      await loadScript(withPrefix('/common.js'));
      await loadScript(withPrefix('/wp-embed.min.js'));
    };

    f();
  }, []);

  return (
    <>
      <Helmet>
        <html lang='ja' />
        <meta charSet='utf-8' />
        <title>{pageTitle}</title>
        <meta name='description' content={pageDescription} />
        <meta
          name='robots'
          content='max-snippet:-1, max-image-preview:large, max-video-preview:-1'
        />
        <link rel='canonical' href={siteMetadata.siteUrl} />
        <meta property='og:site_name' content={siteMetadata.title} />
        <meta property='og:type' content={isTop ? 'website' : 'article'} />
        <meta property='og:title' content={pageTitle} />
        <meta property='og:description' content={pageDescription} />
        <meta property='og:url' content={siteMetadata.siteUrl} />
        <meta property='og:image' content={ogpImage} />
        <meta property='og:image:secure_url' content={ogpImage} />
        <meta name='twitter:card' content='summary_large_image' />
        <meta name='twitter:domain' content='oxwm.net' />
        <meta name='twitter:title' content={pageTitle} />
        <meta name='twitter:description' content={pageDescription} />
        <meta name='twitter:image' content={ogpImage} />
        <link rel='dns-prefetch' href='https://s.w.org/' />
        <meta name='viewport' content='width=device-width,initial-scale=1' />
        <meta name='format-detection' content='telephone=no' />
        {/* @TODO
    <script async="" src="./assets/js"></script>
    <script>
      window.dataLayer = window.dataLayer || [];
      function gtag() {
        dataLayer.push(arguments);
      }
      gtag('js', new Date());
      gtag('config', 'G-3KS46FC4XZ');
    </script>
    */}
      </Helmet>
      {/* body */}
      <div className={`blog ${isTop ? 'home' : page}`} id='body'>
        <div id='l-container'>
          <Header page={page} />

          {isTop || (
            <div className='c-page-header'>
              <div className='c-page-header__inner'>
                <h1 className='c-page-header__title'>{subtitle}</h1>
                <div className='c-page-header__shoulder'>{subtitleEn}</div>
              </div>
            </div>
          )}

          {isTop || (
            <div className='p-breadcrumb'>
              <div className='p-breadcrumb__inner'>
                <p
                  className='p-breadcrumb__item'
                  itemScope=''
                  itemType='https://schema.org/BreadcrumbList'
                >
                  <span
                    itemProp='itemListElement'
                    itemScope=''
                    itemType='https://schema.org/ListItem'
                  >
                    <Link
                      itemType='https://schema.org/Thing'
                      itemProp='item'
                      to='/'
                      title='トップページへ移動'
                    >
                      <span itemProp='name'>TOP</span>
                    </Link>
                    <meta itemProp='position' content='1' />
                  </span>
                  {breadcrumbs.map(({ link, label }, i) => (
                    <React.Fragment key={i}>
                      <span className='p-breadcrumb__item__hr'> &gt; </span>
                      <span
                        itemProp='itemListElement'
                        itemScope=''
                        itemType='https://schema.org/ListItem'
                      >
                        <Link
                          itemType='https://schema.org/Thing'
                          itemProp='item'
                          to={link}
                          title={`${label}へ移動`}
                        >
                          <span itemProp='name'>{label}</span>
                        </Link>
                        <meta itemProp='position' content={i + 2} />
                      </span>
                    </React.Fragment>
                  ))}
                </p>
              </div>
            </div>
          )}
          <div className='l-contents'>{children}</div>
          <Footer />
        </div>
        <div
          id='lightboxOverlay'
          className='lightboxOverlay'
          style={{ display: 'none' }}
        ></div>
        <div id='lightbox' className='lightbox' style={{ display: 'none' }}>
          <div className='lb-outerContainer'>
            <div className='lb-container'>
              <img
                className='lb-image'
                src='data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=='
                alt=''
              />
              <div className='lb-nav'>
                <Link className='lb-prev' to='/?test-prev'></Link>
                <Link className='lb-next' to='/?test-next'></Link>
              </div>
              <div className='lb-loader'>
                <a className='lb-cancel'></a>
              </div>
            </div>
          </div>
          <div className='lb-dataContainer'>
            <div className='lb-data'>
              <div className='lb-details'>
                <span className='lb-caption'></span>
                <span className='lb-number'></span>
              </div>
              <div className='lb-closeContainer'>
                <a className='lb-close'></a>
              </div>
            </div>
          </div>
        </div>
        <div className='l-overlay js-overlay'></div>
      </div>
    </>
  );
}
